@use "../variables.scss";
@use "../mixins.scss";

.radio-variant {
  position: relative;
  display: block;
  padding: 10px 10px 10px 62px;

  &:hover .radio-variant__control {
    background-color: variables.$color-mercury;
  }

  &:active .radio-variant__control {
    background-color: variables.$color-alto-third;
  }

  &:active .radio-variant__control::before {
    opacity: 0.3;
  }

  &:focus-within .radio-variant__control {
    background-color: variables.$color-mercury;
  }
}

.radio-variant__control {
  position: absolute;
  left: 0;
  top: calc(50% - 23px);
  width: 46px;
  height: 46px;
  border-radius: 50%;
  background-color: variables.$color-concrete;
}

.radio-variant__input:checked + .radio-variant__control::before {
  position: absolute;
  left: calc(50% - 8px);
  top: calc(50% - 8px);
  width: 16px;
  height: 16px;
  content: "";
  border-radius: 50%;
  background-color: variables.$color-mine-shaft;
}

.radio-variant__label {
  @include mixins.heading-font();
  text-transform: none;
}
